<template>
  <div class="home">
    <div class="top"></div>
    <div>
      <van-search placeholder="请输入搜索关键词" />
    </div>
    <div class="banner">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item>
          <img src="../../assets/images/d3.jpg" class="auto-img" alt="">
        </van-swipe-item>
        <van-swipe-item>
          <img src="../../assets/images/d9.jpg" class="auto-img" alt="">
        </van-swipe-item>
        <van-swipe-item>
          <img src="../../assets/images/e3.jpg" class="auto-img" alt="">
        </van-swipe-item>
        <van-swipe-item>
          <img src="../../assets/images/ef.jpg" class="auto-img" alt="">
        </van-swipe-item>
      </van-swipe>
    </div>
    <div>
      <div class="classify-box">
        <ul class="clearfix">
          <li class="classify">
            <span></span>
            <span>快捷购票</span>
          </li>
          <li class="classify">
            <span></span>
            <span>即将上映</span>
          </li>
          <li class="classify">
            <span></span>
            <span>在线充值</span>
          </li>
          <li class="classify">
            <span></span>
            <span>电影资讯</span>
          </li>
        </ul>
      </div>
      <!-- 新片榜 -->
      <div class="new-movie-list">
        <div class="new-movie-title clearfix">
          <span class="fl">新片榜</span>
          <span class="fr" @click="changeNewMovieData">换一换</span>
        </div>
        <div class="new-movie-content">
          <div class="new-movie clearfix">
            <div class="new-movie-item fl" v-for="(item, index) in showNewMovieData.data" :key="index" :id="item.id" @click="viewMovieDetail({name: 'movieDetail', params: {id: item.id}})">
              <div class="movie-img">
                <img :src="item.images.small" alt="" class="auto-img">
              </div>
              <div class="new-movie-name one-text">{{item.title}}</div>
              <div class="score clearfix">
                <div class="fl clearfix star-box-count">
                  <!-- 灰星 -->
                  <div ref="noactivestar" class="noactive-star fl">
                    <span>
                      <van-icon name="star-o" />
                    </span>
                    <span>
                      <van-icon name="star-o" />
                    </span>
                    <span>
                      <van-icon name="star-o" />
                    </span>
                    <span>
                      <van-icon name="star-o" />
                    </span>
                    <span>
                      <van-icon name="star-o" />
                    </span>
                  </div>
                  <!-- 黄星 -->
                  <div class="active-star" :style="{width: item.width + 'px'}">
                    <span>
                      <van-icon name="star-o" />
                    </span>
                    <span>
                      <van-icon name="star-o" />
                    </span>
                    <span>
                      <van-icon name="star-o" />
                    </span>
                    <span>
                      <van-icon name="star-o" />
                    </span>
                    <span>
                      <van-icon name="star-o" />
                    </span>
                  </div>
                </div>
                <!-- 分数 -->
                <div class="fraction fr">{{item.rating.average}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {createNamespacedHelpers} from 'vuex'
  const {mapState} = createNamespacedHelpers('homeModule');

  export default {
    name: 'home',

    computed: {
      ...mapState(['baseUrl', 'newMovieData', 'showNewMovieData', 'isHasData', 'isInit'])
    },

    methods: {
      // 换一换数据
      changeNewMovieData() {
        if (this.isHasData) {
          // console.log('已更换数据');
          // 提交换一换数据
          this.$store.commit('homeModule/toggleNewMovieData')
        }
      },

      //查看电影详情
      viewMovieDetail(o) {
        this.$router.push(o);
      }
    },

    created() {

      if (!this.isInit) {
        return;
      }

      //开启加载提示
      this.$toast.loading({
        duration: 0,
        message: '加载中...'
      });

      // 初始化新片榜数据
      this.axios({
        method: 'GET',
        url: this.baseUrl + '/new_movies'
      }).then(result => {

        result.data.subjects.forEach((v) => {
          v.width = 0;
        })

        // console.log('result :', result);
        this.$store.dispatch('homeModule/getNewMovieData', result.data.subjects)
          .then(() => {
            // 根据星级平均分设置宽度
            let width = this.$refs.noactivestar[0].clientWidth;
            // console.log('width :', width);

            //提交modifyNewMovieData
            this.$store.commit('homeModule/modifyNewMovieData', width);

            this.$toast.clear();
          })

      }).catch( err => {
        // console.log('err :', err);
        this.$toast.clear();
      })
    }
  }
</script>

<style lang="less" scoped>
  .home {
    width: 100%;
    .top {
      position: sticky;
      top: 0;
      left: 0;
      height: 24px;
      background-color: #000;
    }
    .banner {
      width: 100%;
      
      .auto-img {
        height: 200px;
        // border-radius: 10px;
        overflow: hidden;
      }
    }
    .classify-box {

      ul {
        padding: 10px 15px;
        background-color: #fff;
      }
      .classify {
        float: left;
        width: 25%;
        text-align: center;

        &:nth-child(1) {
          span {
            &:first-child {
              margin: auto;
              display: block;
              width: 48px;
              height: 48px;
              background: url('../../assets/images/purchase.png') no-repeat center center;
              background-size: 26px 26px;
              background-color: #FF7100;
              border-radius: 50%;
            }
          }
        }
        &:nth-child(2) {
          span {
            &:first-child {
              margin: auto;
              display: block;
              width: 48px;
              height: 48px;
              background: url('../../assets/images/film.png') no-repeat center center;
              background-size: 32px 32px;
              background-color: #3EB9E3;
              border-radius: 50%;
            }
          }
        }
        &:nth-child(3) {
          span {
            &:first-child {
              margin: auto;
              display: block;
              width: 48px;
              height: 48px;
              background: url('../../assets/images/recharge.png') no-repeat center center;
              background-size: 24px 24px;
              background-color: #29B16F;
              border-radius: 50%;
            }
          }
        }
        &:nth-child(4) {
          span {
            &:first-child {
              margin: auto;
              display: block;
              width: 48px;
              height: 48px;
              background: url('../../assets/images/information.png') no-repeat center center;
              background-size: 30px 30px;
              background-color: #FFD301;
              border-radius: 50%;
            }
          }
        }
      }
    }

    .new-movie-list {
      width: 100%;
      .new-movie-title {
        font-size: 16px;
        padding: 10px 12px;
      }
      .new-movie-content {
        background-color: #fff;
        padding: 10px 12px;
        overflow-x: auto;
        overflow-y: hidden;
      }
      .new-movie-item {
        width: 100px;
        text-align: center;
        margin-right: 10px;
        // overflow: hidden;
        &:last-child {
          margin-right: 0;
        }
      }
      .new-movie {
        width: 890px;
      }
      
      .movie-img {
        width: 100px;
        height: 140px;
        border-radius: 10px;
        overflow: hidden;
        .auto-img {
          height: 100%;
        }
      }
      .score {
        position: relative;
        font-size: 16px;
        text-align: center;
      }
      .active-star {
        position: absolute;
        top: 0;
        left: 0;
        color: #FDC03D;
        height: 16px;
        width: 0;
        overflow: hidden;
      }
      .fraction {
        font-size: 14px;
        // margin-left: 6px;
        color: #FDC03D;
        width: 20px;
      }
      .noactive-star {
        width: 80px;
        height: 16px;
      }
      .star-box-count {
        width: 80px;
        position: relative;
        span {
          position: absolute;
          width: 16px;
          height: 16px;
          top: 0;
          &:nth-child(1){
          left: 0;
          }
          &:nth-child(2){
            left: 16px;
          }
          &:nth-child(3){
            left: 32px;
          }
          &:nth-child(4){
            left: 48px;
          }
          &:nth-child(5){
            left: 64px;
          }
        }
      }
      .new-movie-name {
        padding: 5px 0;
      }
      
    }
  }
</style>